@import 'fonts.scss';
@import 'var.scss';
@import 'media.scss';


body {
	margin: 0;
	padding: 0;
	color: $iroko;
	font-size: 16px;
	font-family: 'Montserrat', sans-serif;
	overflow-x: hidden;
}
a {
	text-decoration: none;
	color: inherit;
}
ul {
	list-style: none;
	padding: 0;
}
.title {
	font-size: 38px;
	font-family: "Museo Cyrillic -700";
	position: relative;
	padding-bottom: 58px;
	margin-bottom: 90px;
	@media (max-width: 520px) {
		font-size: 32px;
		padding-bottom: 40px;
		margin-bottom: 60px;
	}
	@media (max-width: 320px) {
		font-size: 26px;
	}
	&:after {
		content: '';
		display: block;
		position: absolute;
		width: 52px;
		height: 4px;
		background-color: $laRioja;
		bottom: 0;
		left: 50%;
		margin-left: -26px;
	}
}
.btn {
	border-radius: 10px;
	font-family: "Museo Cyrillic -700";
	font-size: 24px;
	color: $iroko;
	border: none;
	cursor: pointer;
	transition: all 0.3s ease;
	&--gold {
		background-color: $ronchi;
		box-shadow: 0 5px 16px rgba(242, 197, 74, 0.64);
		padding: 24px 42px;
		@media (max-width: 520px) {
			padding: 18px 45px;
		}
		&:hover {
			background-color: darken($ronchi,5%);
			box-shadow: 0 5px 16px rgba(5, 5, 5, 0.64);
		}
	}
	&--green {
		color: #fff;
		background-color: $laRioja;
		padding: 15px 50px;
		&:hover {
			background-color: darken($laRioja,5%);
			box-shadow: 0 5px 16px rgba(122, 122, 122, 0.64);
		}
	}
	&--white {
		background-color: #ffffff;
		padding: 15px 50px;
		box-shadow: 0 5px 16px rgba(98, 98, 98, 0.64);
		&:hover {
			background-color: darken(#ffffff,5%);
			box-shadow: 0 5px 16px rgba(5, 5, 5, 0.64);
		}
	}
}
.main-screen{
	background-image: url(../images/main-bg768.jpg);
	background-size: cover;
	@media (min-width: 768px) {
		background-image: url(../images/main-bg.jpg);
	}
	&__bg {
		background-image: url(../images/main-bg-wr.png);
		background-size: cover;
		background-attachment: fixed;
		@media (max-width: 1100px) {
			background-image: none;
		}
	}
	&-content {
		max-width: $wrap;
		margin: 0 auto;
		padding: 50px 10px 300px;
		background-image: url(../images/mockup-phone-main.png);
		background-position: bottom right;
		background-repeat: no-repeat;
		@media (max-width: 950px) {
			background-position-x: right -180px;
		}
		@media (max-width: 768px) {
			background-image: none;
		}
		@media (max-width: 520px) {
			padding-bottom: 200px;
		}
		&__text-wrap {
			width: 70%;
			@media (max-width: 950px) {
				width: 100%;
			}
		}
	}
	&__title {
		width: 60%;
		color: #ffffff;
		// font-size: 78px;
		// line-height: 83.42px;
		font-size: 58px;
		font-family: "AmazingGrotesk";
		@media (max-width: 768px) {
			width: auto;
			text-align: center;
			box-sizing: border-box;
		}
		@media (max-width: 520px) {
			font-size: 40px;
			margin-bottom: 40px;
		}
	}
	&__btn-block {
		display: flex;
		align-items: center;
		@media (max-width: 768px) {
			flex-direction: column;
		}
		// span {
		// 	color: #ffffff;
		// 	font-size: 16px;
		// 	line-height: 26px;
		// 	margin: 0 23px 0 43px;
		// 	margin-bottom: 30px;
		// 	@media (max-width: 768px) {
		// 		text-align: center;
		// 	}
		// }
		&-description {
			color: #ffffff;
			font-size: 16px;
			line-height: 26px;
			margin: 0 15px 0 23px;
			margin-bottom: 30px;
			@media (max-width: 768px) {
				text-align: center;
			}
			span {
				font-weight: 700;
				display: block;
			}
		}
	}
	&__btn {
		margin-bottom: 30px;
		@media (max-width: 360px) {
			font-size: 20px;
		}
	}
	&__play {
		background-image: url(../images/play.svg);
		width: 124px;
		height: 124px;
		transition: all 0.5s ease;
		&:hover {
			transform: scale(1.1);
		}
	}
}
.header {
	max-width: $wrap;
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	padding: 20px 10px 16px 10px;
	box-sizing: border-box;
}

.logo-block {
	display: flex;
	align-items: center;
	&__item {
		&:first-child {
			margin-right: 64px;
			@media (max-width: 520px) {
				margin-right: 30px;
			}
		}
		@media (max-width: 768px) {
			width: 30%;
		}
		img {
			width: 100%;
		}
	}
}

.social-icon-block {
	&__list {
		display: flex;
	}
	&__item {
		margin-right: 43px;
		&:last-child {
			margin-right: 0;
		}
		@media (max-width: 520px) {
			margin-right: 20px;
		}
		i {
			color: #fff;
			font-size: 20px;
			transition: all 0.3s ease;
			&:hover {
				color: $ronchi;
			}
		}
	}
}

.purpose {
	background-image: url(../images/white-bg.png);
	position: top;
	margin-top: -200px;
	padding-top: 250px;
	margin-bottom: 60px;
	&__list {
		width: $wrap + 300px;
		margin: 0 auto;
		display: flex;
		justify-content: center;
		@media (max-width: 1500px) {
			flex-wrap: wrap;
			width: 100%;
		}
		@media (max-width: 768px) {
			flex-direction: column;
			width: 100%;
		}
	}
	&-item {
		width: 30%;
		box-sizing: border-box;
		padding: 130px 20px;
		@media (max-width: 1510px) {
			width: 25%;
		}
		@media (max-width: 1200px) {
			width: 50%;
			padding: 130px 80px;
		}
		@media (max-width: 900px) {
			padding: 130px 40px;
		}
		@media (max-width: 768px) {
			width: 100%;
			padding: 30px;
		}
		i {
			font-size: 36px;
		}
		&.active {
			width: 40%;
			padding: 130px 30px 50px 137px;
			background-image: url(../images/mockup-purpose.png);
			background-position: top left;
			background-repeat: no-repeat;
			color: #fff;
			@media (max-width: 1500px) {
				flex-wrap: wrap;
				width: 600px;
			}
			@media (max-width: 768px) {
				width: 100%;
				background-position: center top;
				padding-left: 10px;
				padding-right: 10px;
				margin-bottom: 30px;
			}
			@media (max-width: 440px) {
				background-image: none;
				color: $iroko;
				padding-top: 30px;
			}
			.purpose-item__title::after {
				background-color: #fff;
				@media (max-width: 440px) {
					background-color: $laRioja;
				}
			}
		}
		&__text-wrap {
			width: 80%;
			@media (max-width: 768px) {
				width: 385px;
				margin: 0 auto;
				box-sizing: border-box;
				padding-left: 30px;
			}
			@media (max-width: 440px) {
				padding-left: 0px;
				width: 100%;
			}
		}
		&__title {
			font-family: "Museo Cyrillic -700";
			font-size: 30px;
			position: relative;
			padding-bottom: 26px;
			margin: 20px 0;
			@media (max-width: 520px) {
				font-size: 28px;
			}
			&:after {
				content: '';
				display: block;
				position: absolute;
				width: 52px;
				height: 4px;
				background-color: $laRioja;
				bottom: 0;
				left:0;
			}
		}
		&__description {
			line-height: 26px;
			margin-bottom: 40px;
			li {
				padding-left: 20px;
				position: relative;
				margin-bottom: 15px;
				&:before {
					content: '';
					width: 4px;
					height: 4px;
					background-color: #fff;
					display: block;
					position: absolute;
					top: 10px;
					left: 0;
				}
				&.green {
					&:before {
						background-color: #b5c616;
					}
				}
			}
		}
		&__btn {
			margin: 0 auto;
			display: block;
			text-align: center;
		}
	}
}

.steps {
	text-align: center;
	padding-bottom: 100px;
	@media (max-width: 768px) {
		padding-bottom: 0;
	}
	&__list {
		display: flex;
		max-width: $wrap;
		margin: 0 auto;
		@media (max-width: 768px) {
			flex-direction: column;
			align-items: center;
		}
	}
	&-item {
		width: 400px;
		border-right: 1px solid #d7d7d7;
		@media (max-width: 768px) {
			border-right: none;
			border-bottom: 1px solid #d7d7d7;
			margin-bottom: 30px;
			&:last-child {
				border-bottom: none;
			}
		}
		&:last-child {
			border-right: none;
		}
		&__icon {
			margin: 0 auto;
			margin-top: 43px;
			&.registration {
				background-image: url(../images/step1.png);
				width: 141px;
				height: 107px;
			}
			&.lesson {
				background-image: url(../images/step2.png);
				width: 127px;
				height: 126px;
			}
			&.diplom {
				background-image: url(../images/step3.png);
			  width: 95px;
			  height: 146px;
			}
		}
		&__wrap-icon {
			width: 100%;
			height: 235px;
			background-repeat: no-repeat;
			background-position: center;
			display: flex;
			&.registration {
				background-image: url(../images/step1.svg);
			}
			&.lesson {
				background-image: url(../images/step2.svg);
			}
			&.diplom {
				background-image: url(../images/step3.svg);
			}
		}
		&__title {
			font-family: "Museo Cyrillic -700";
			font-size: 27px;
			line-height: 38.81px;
			margin-bottom: 27px;
		}
		&__description {
			font-size: 11px;
			line-height: 22px;
			padding-bottom: 30px;
			width: 60%;
			margin: 0 auto;
		}
	}
}

.test {
	padding: 30px 10px 30px 10px;
	background-image: url(../images/test-bg.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	// margin-bottom: 50px;
	@media (max-width: 768px) {
		padding: 80px 10px;
	}
	&__wrap {
		max-width: $wrap;
		margin: 0 auto;
		display: flex;
	}
	&__text {
		padding-top: 90px;
		@media (max-width: 768px) {
			padding-top: 0px;
		}
	}
	&__title {
		font-size: 48px;
		font-family: "Museo Cyrillic -700";
		position: relative;
		padding-bottom: 26px;
		margin-bottom: 36px;
		width: 60%;
		@media (max-width: 1100px) {
			width: 80%;
		}
		@media (max-width: 768px) {
			font-size: 34px;
			width: 100%;
		}
		@media (max-width: 320px) {
			font-size: 26px;
		}
		&:after {
			content: '';
			display: block;
			position: absolute;
			width: 52px;
			height: 4px;
			background-color: $laRioja;
			bottom: 0;
			left:0;
		}
	}
	&__description {
		width: 40%;
		margin-bottom: 60px;
		@media (max-width: 1100px) {
			width: 70%;
		}
		@media (max-width: 768px) {
			width: 100%;
		}
	}
	&__btn {
		display: inline-block;
		margin-bottom: 20px;
	}
	&__img {
		position: relative;
		bottom: 0px;
		box-shadow: 0 5px 16px rgba(242, 197, 74, 0.64);
		transition: all 0.5s ease;
		@media (max-width: 768px) {
			display: none;
		}
		&:hover {
			transform: translateY(-15px);
		}
	}
}


.film {
	padding-top: 20px;
	padding-bottom: 60px;
	text-align: center;
	background-image: url(../images/film-bg.png);
	background-size: cover;
	background-repeat: no-repeat;
	&__wrap {
		max-width: $wrap;
		margin: 0 auto;
	}
	&-video {
		max-width: 1204px;
		position: relative;
		margin-bottom: 30px;
		img {
			width: 100%;
		}
		&__play {
			width: 146px;
			height: 146px;
			background-image: url(../images/play-film.png);
			background-color: transparent;
			border: none;
			position: absolute;
			top: 50%;
			margin-top: -73px;
			left: 50%;
			margin-left: -73px;
			cursor: pointer;
			transition: all 0.5s ease;
			@media (max-width: 550px) {
				width: 86px;
				height: 86px;
				background-size: 100%;
				margin-top: -43px;
				margin-left: -43px;
			}
			&:hover {
				transform: scale(1.1);
			}
		}
	}
}
.numbers {
	background-color: $laRioja;
	color: #fff;
	&__wrap {
		max-width: $wrap;
		margin: 0 auto;
	}
	&__list {
		display: flex;
		justify-content: space-between;
		@media (max-width: 1200px) {
			flex-wrap: wrap;
		}
		@media (max-width: 768px) {
			flex-direction: column;
		}
	}
	&__item {
		border-right: 1px solid #fff;
		padding: 50px 50px 40px;
		flex-grow: 1;
		transition: all 0.5s ease;
		@media (max-width: 1170px) {
			border-bottom: 1px solid #fff;
		}
		@media (max-width: 520px) {
			padding: 30px;
		}
		h3 {
			font-size: 40px;
			margin: 0;
			@media (max-width: 520px) {
				font-size: 34px;
			}
			@media (max-width: 320px) {
				font-size: 26px;
			}
		}
		span {
			font-size: 58px;
			@media (max-width: 520px) {
				font-size: 40px;
			}
		}
		p {
			margin: 0;
		}
		&--sum {
			background-image: url(../images/numbers-item-bg.svg);
			background-repeat: no-repeat;
			text-align: center;
			padding-top: 70px;
			@media (max-width: 520px) {
				padding-top: 40px;
				padding-bottom: 40px;
			}
			&:hover {
				background-color: darken($laRioja,3%);
			}
		}
		&--title {
			width: 330px;
			box-sizing: border-box;
			@media (max-width: 920px) {
				width: 100%;
				text-align: center;
			}
		}
	}
}
.map {
	width: 100%;
	padding-bottom: 116px;
	position: relative;
	overflow: hidden;
	@media (max-width: 768px) {
		padding-bottom: 50px;
		margin-bottom: 30px;
	}
	&__bg {
		background-image: url(../images/white-bg.png);
		width: 100%;
		height: 250px;
		display: block;
		position: absolute;
		bottom: 100px;
		@media (max-width: 768px) {
			bottom: 0px;
		}
		@media (max-width: 690px) {
			bottom: 0px;
			background-position-x: -280px;
		}
	}
	&__img {
		width: 100%;
		img {
			width: 100%;
			@media (max-width: 520px) {
				width: auto;
			}
		}
	}
	&__girl {
		position: absolute;
		bottom: 85px;
		left: 20%;
		@media (max-width: 940px) {
			width: 300px;
			left: 10%;
		}
		@media (max-width: 768px) {
			left: 10%;
			width: 200px;
			bottom: 0px;
		}
		img {
			width: 190px;
		}
	}
}
.photo {
	text-align: center;
	margin-bottom: 100px;
	&__link {
		margin-top: 45px;
		display: inline-block;
		font-size: 17px;
		text-decoration: underline;
		@media (max-width: 320px) {
			font-size: 14px;
		}
		&:hover {
			text-decoration: none;
		}
	}
	.slick {
		&-arrow {
			background-image: url(../images/sprite.png);
			background-color: transparent;
			border: none;
			font-size: 0;
			position: absolute;
			bottom: -55px;
			width: 60px;
			height: 13px;
			cursor: pointer;
		}
		&-prev {
			background-position: 0px -13px;
			left: 10%;
			@media (max-width: 520px) {
				left: 2%;
			}
		}
		&-next {
			background-position: 0px 0px;
			right: 10%;
			@media (max-width: 520px) {
				right: 2%;
			}
		}
	}
}


.footer {
	background-color: $tomThumb;
	&__top {
		max-width: $wrap;
		padding: 37px 10px 30px 10px;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		border-bottom: 1px solid #6e836b;
		background-image: url(/images/footer-top_bg.png);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: left 28px center;
		@media (max-width: 1024px) {
			flex-direction: column;
			align-items: center;
		}
	}

	&__bottom {
		max-width: $wrap;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20px 0;
		@media (max-width: 768px) {
			flex-direction: column;
			.logo-block {
				justify-content: center;
			}
		}
		.logo-block {
			align-items: center;
			@media (max-width: 560px) {
				flex-direction: column;
			}
			span {
				color: #ffffff;
				opacity: 0.4;
				font-size: 14px;
				display: block;
				margin-right: 38px;
				@media (max-width: 560px) {
					margin-bottom: 20px;
					margin-right: 0;
				}
			}
			.logo-block__item {
				margin-right: 40px;
				@media (max-width: 768px) {
					width: auto;
					img {
						width: auto;
					}
				}
				@media (max-width: 560px) {
					margin-bottom: 20px;
					margin-right: 0;
				}
			}
		}
	}

	&__logo {
		.logo-block__item:first-child {
			margin-right: 40px;
		}
		@media (max-width: 1024px) {
			margin-bottom: 40px;
			justify-content: center;
		}
	}
	&-contacts {
		display: flex;
		align-items: center;
		@media (max-width: 1024px) {
			align-items: center;
		}
		&__phone {
			color: #fff;
			font-size: 21px;
			margin-bottom: 23px;
		}
	}
	&__text {
		max-width: 351px;
		text-shadow: 0 3px 10px rgba(48, 78, 44, 0.6);
		color: #ffffff;
		font-family: "Museo Cyrillic -700";
		font-size: 24px;
		text-transform: uppercase;
		text-align: center;
		@media (max-width: 1024px) {
			margin-bottom: 30px;
		}
	}
	&__link {
		color: #a1c93b;
		display: block;
		margin-left: 30px;
		text-decoration: underline;
		font-size: 18px;
		&:hover {
			text-decoration: none;
		}
	}
}

.copyright {
	color: #ffffff;
	opacity: 0.4;
	font-size: 14px;
	display: block;
	@media (max-width: 768px) {
		margin-bottom: 30px;
	}
	@media (max-width: 560px) {
		order: 2;
		margin-bottom: 0;
		margin-top: 10px;
	}
}
